<template>
  <div
    v-bind:style="[
      !this.is_selected
        ? {
            transform:
              'translate(' + this.piece_height + '%,' + this.piece_width + '%)'
          }
        : {
            top: this.mouse_top_pos + 'px',
            left: this.mouse_left_pos + 'px',
            zIndex: 3
          }
    ]"
  ></div>
</template>

<script>
export default {
  name: "Piece",
  props: {
    position: { x: String, y: String },
    pieceName: String,
    pieceId: Number
  },
  data: function() {
    return {
      id: this.pieceId,
      name: this.pieceName,
      is_selected: false,
      mouse_top_pos: 0,
      mouse_left_pos: 0,
      piece_height: this.position.x,
      piece_width: this.position.y
    };
  },
  watch: {
    position: function(newVal) {
      this.piece_height = newVal.x;
      this.piece_width = newVal.y;
    }
  },

  methods: {
    got_selected(isTouchInput) {
      window.console.log(isTouchInput);
      this.is_selected = true;
      if (isTouchInput) this.$el.classList.toggle("selected_m");
      else this.$el.classList.toggle("selected");
    },
    got_unselected(isTouchInput) {
      if (isTouchInput) this.$el.classList.toggle("selected_m");
      else this.$el.classList.toggle("selected");

      this.is_selected = false;
    },
    set_mouse_pos(top, left) {
      this.mouse_top_pos = top;
      this.mouse_left_pos = left;
    },
    setPosition(y, x) {
      this.piece_height = x;
      this.piece_width = y;
    }
  }
};
</script>

