                  <h1>Challenge a Human</h1>
                </v-col>
                <v-col cols="12" sm="6" md="6" class="d-flex justify-center">
                  <v-btn-toggle v-model="seek.rated" borderless>
                    <v-btn :value="false">Unrated</v-btn>

                    <v-btn :value="true">Rated</v-btn>
                  </v-btn-toggle>
                </v-col>
                <v-col cols="12" sm="6" md="6" order-md="2">
                  <v-select v-model="seek.color" :items="['random','white','black']" label="Color"></v-select>
                </v-col>
                <v-col sm="12" md="6" cols="12">
                  <v-subheader>Time: {{seek.time}} minutes</v-subheader>
                  <v-slider v-model="seek.time" prepend-icon="av_timer" :min="0" :max="15"></v-slider>
                </v-col>

                <v-col md="6" cols="12" order-md="4">
