                    :min="600"
                  ></v-range-slider>
                </v-col>
                <v-col md="6" cols="12" order-md="6" class="d-flex justify-center">
                  <v-btn
                    class="ma-2"
                    tile
                    x-large
                    color="primary"
                    dark
                    @click="searchGame()"
                  >Find Game</v-btn>
                </v-col>
              </v-row>
            </v-form>
          </v-row>
        </v-col>
        <v-col cols="10" sm="10" md="5" offset-md="1">
          <v-row>
            <v-form ref="form">
              <v-row justify="center">
                <v-col cols="12" class="d-flex justify-center">
                  <h1>Challenge Stockfish</h1>
                </v-col>

                <v-col cols="12" md="12" class="d-flex justify-center">
                  <v-btn-toggle v-model="seekAI.level" color="primary" background-color="secondary">
                    <v-btn>1</v-btn>

                    <v-btn>2</v-btn>

                    <v-btn>3</v-btn>

                    <v-btn>4</v-btn>

                    <v-btn>5</v-btn>

                    <v-btn>6</v-btn>

                    <v-btn>7</v-btn>

                    <v-btn>8</v-btn>
                  </v-btn-toggle>
                </v-col>
                <v-col cols="12" sm="12" md="12">
                  <v-select
                    v-model="seekAI.color"
                    :items="['random','white','black']"
                    label="Color"
                  ></v-select>
                </v-col>
                <v-col md="6" cols="12" order-md="6" class="d-flex justify-center">
                  <v-btn
                    class="ma-2"
                    tile
                    x-large
                    color="primary"
                    dark
                    @click="challengeStockfish()"
                  >Challenge Stockfish</v-btn>
                </v-col>
              </v-row>
            </v-form>
          </v-row>
        </v-col>
      </v-row>
    </template>
    <template v-else>
      <v-row justify="center" align="center" style="padding-top:10%;">
        <v-col cols="12" class="d-flex justify-center">
          <v-progress-circular :size="200" :width="10" color="primary" indeterminate></v-progress-circular>
        </v-col>
        <v-col class="d-flex justify-center">
          <h2>Looking for a match...</h2>
        </v-col>
      </v-row>
    </template>
  </v-container>
</template>

<script>
export default {
  name: "GameFinder",
  data: function() {
    return {
      seek: {
        rated: false,
        time: 0, // minuti
        increment: 0, // secondi
        color: "random",
        rating_range: [1200, 2000]
      },
      seekAI: {
        level: 0,
        color: "random"
      },
      isSearchingForGame: false
    };
  },
  methods: {
    searchGame: async function() {
      this.isSearchingForGame = true;
      //const axios = require("axios");
      //const qs = require("querystring");
      /*await axios
        .post(
          "/api/board/seek",
          qs.stringify({
            rated: this.seek.rated,
            time: this.seek.time,
            increment: this.seek.increment,
            color: this.seek.color,
            ratingRange:
              this.seek.rating_range[0] + "-" + this.seek.rating_range[1]
          }),
          {
            baseURL: "https://lichess.org/",
            headers: {
              Authorization: "Bearer " + this.$store.getters.tokens
            }
          }
        )
        .catch(function() {
          this.isSearchingForGame = false;
        });*/
      /*
      await axios({
        method: "post",
        url: "https://lichess.org/api/board/seek",
        data: qs.stringify({
          rated: this.seek.rated,
          time: this.seek.time,
          increment: this.seek.increment,
          color: this.seek.color,
          ratingRange:
            this.seek.rating_range[0] + "-" + this.seek.rating_range[1]
        }),
        headers: {
          Authorization: "Bearer " + this.$store.getters.tokens,
          "Content-Type": "application/x-www-form-urlencoded"
        }
      }).catch(() => {
        this.isSearchingForGame = false;
      }); // non funzia per motivi strani.*/
    },
    challengeStockfish: async function() {
      const axios = require("axios");
      const qs = require("querystring");

      await axios({
        method: "post",
        url: "https://lichess.org/api/challenge/ai",
        data: qs.stringify({
          level: this.seekAI.level + 1,
          color: this.seekAI.color,
          ratingRange:
            this.seek.rating_range[0] + "-" + this.seek.rating_range[1]
        }),
        headers: {
          Authorization: "Bearer " + this.$store.getters.tokens,
          "Content-Type": "application/x-www-form-urlencoded"
        }
      }).catch(() => {
        this.isSearchingForGame = false;
      });
    }
  },
  computed: {
    getTime() {
      if (this.seek.increment == 0) return "None";
      let minutes = Math.floor(this.seek.increment / 60);
      let toRtn = "";

      if (minutes != 0) toRtn = minutes + " minutes";
      let seconds = this.seek.increment - minutes * 60;
      if (seconds != 0) {
        if (minutes != 0) toRtn = toRtn + " " + seconds + " seconds";
        else {
          toRtn = seconds + " seconds";
        }
      }
      return toRtn;
    }
  }
};
</script>

