<template>
  <v-container>
    <template>
      <template>
        <v-content>
          <v-row>
            <Chessboard ref="chessboard" />
          </v-row>
        </v-content>
      </template>
    </template>
  </v-container>
</template>

<script>
import Chessboard from "./Chessboard.vue";
export default {
  name: "Game",
  components: { Chessboard },

  data: function() {
    return {
      id: undefined
    };
  },

  /*mounted: function() {

    
    if (this.$store.getters.tokens == null) return;
    this.id = this.$route.params.id;

    window.console.log(this.id);
    if (this.id == undefined) {
      window.console.log("Creiamo la partita");
      this.$router.push("/find_game");
    } else {
      window.console.log("gioco cominciato");

      this.listenForGameState();
    }
  },*/
  methods: {
    changeID: function(id) {
      this.id = id;
      this.listenForGameState();
    },
    sendEvent: async function(event) {
      if (event.type == "move") {
        const axios = require("axios");

        await axios({
          method: "post",
          url:
            "https://lichess.org/api/board/game/" +
            this.id +
            "/move/" +
            event.move,

          headers: {
            Authorization: "Bearer " + this.$store.getters.tokens,
            "Content-Type": "application/x-www-form-urlencoded"
          }
        }).catch(() => {
          window.console.log("REDO");
          return null;
        }); // non funzia per motivi strani.
        return true;
      }
    },
    gotEvent: function(event) {
      window.console.log(event);
      if (this.has_game_started) {
        window.console.log(this.$refs["chessboard"]);

        if (event.type == "gameState" && event.status == "started") {
          let moves = event.moves.split(" ");

          let isThisWhiteMove = moves.length % 2 == 0;

          window.console.log(
            isThisWhiteMove + " - " + this.game.is_player_white
          );
          //
          // n mosse          0 1 2 3 4 5 6 7 8
          // tocca al bianco  1 0 1 0 1 0 1 0 1
          //
          if (
            (isThisWhiteMove && this.game.is_player_white) ||
            (!isThisWhiteMove && !this.game.is_player_white)
          ) {
            this.$refs["chessboard"].makeMove(moves[moves.length - 1]);
          }

          //this.$refs["chessboard"].updateState(moves);
        }
      } else {
        //check if player is white or not
        if (event.type == "gameFull") {
          //according to apis: The first line is always of type gameFull.

          this.has_game_started = true;
          window.console.log("Game started");
          window.console.log(this.$parent.$parent.$parent.account.id);
          window.console.log(event.black.id);
          if (this.$parent.$parent.$parent.account.id == event.black.id) {
            this.game.is_player_white = false;
            this.$refs["chessboard"].is_player_white = false;
            window.console.log("Is player white");
            this.$refs["chessboard"].flipBoard();
          }

          if (event.state.moves != "") {
            //ci sono mosse in gamefull,  stata ricaricata la pagina.
            let moves = event.state.moves.split(" ");
            moves.forEach(move => {
              this.$refs["chessboard"].makeMove(move);
            });
          }
        }
      }
    },
    listenForGameState: async function() {
      const ndjsonStream = require("can-ndjson-stream");
      fetch("https://lichess.org/api/board/game/stream/" + this.id, {
        headers: {
          Authorization: "Bearer " + this.$store.getters.tokens
        }
      })
        .then(response => {
          window.console.log("response");
          return ndjsonStream(response.body); //ndjsonStream per leggere response.body
        })
        .then(exampleStream => {
          const reader = exampleStream.getReader();
          let read;
          reader.read().then(
            (read = result => {
              if (result.done) {
                return;
              }
              //new event found.

              this.gotEvent(result.value);
              reader.read().then(read);
            })
          );
        })
        .catch(e => {
          window.console.log("Errore: " + e);
        });
    }
  }
};
</script>
