<template>
  <div
    v-bind:class="'square ' + getClass()"
    v-bind:style="{
      transform:
        'translate(' + this.piece_height + '%,' + this.piece_width + '%)'
    }"
  ></div>
</template>

<script>
export default {
  name: "Square",
  props: {
    position: { x: String, y: String },
    attacked: Boolean
  },
  data: function() {
    return {
      piece_height: this.position.x,
      piece_width: this.position.y,
      is_attack: this.attacked
    };
  },
  methods: {
    getClass: function() {
      return this.is_attack ? "attacked" : "not_attacked";
    }
  }
};
</script>

<style>
.square {
  position: absolute;

  top: 0;

  left: 0;

  width: 12.5%;

  height: 12.5%;

  background-size: cover;

  z-index: 0;

  will-change: transform;
}

.square.attacked {
  background: radial-gradient(
    transparent 0%,
    transparent 80%,
    rgba(20, 85, 0, 0.3) 80%
  );
  z-index: 2;
}

.square.not_attacked {
  background: radial-gradient(rgba(20, 85, 30, 0.5) 19%, rgba(0, 0, 0, 0) 20%);
}

.square:hover,
.square:active {
  background: rgba(20, 85, 30, 0.3);
}
</style>
