<template>
  <v-row>
    <v-col cols="12" md="7">
      <div :style="'margin-top:-6%;padding: 0 '+board_padding">
        <div class="chessboard">
          <div class="wrap" ref="chessBoard">
            <div>
              <div class="helper">
                <div class="ctr">
                  <div
                    class="cbrd"
                    @mousedown="got_selected"
                    @mousemove="while_selected"
                    @touchstart="got_selected"
                    @touchmove="while_selected"
                    @mouseup="stopDrag"
                    @touchend="stopDrag"
                  >
                    <Piece
                      v-for="piece in list_pieces"
                      :ref="piece.id"
                      :key="piece.id"
                      :position="
                    setPieceLocationInBoard(piece.position.x, piece.position.y)
                  "
                      v-bind:pieceName="piece.type + piece.color"
                      v-bind:pieceId="piece.id"
                      :class="piece.type + piece.color + ' ' + piece.id"
                    />
                    <Square
                      v-for="(move, index) in valid_moves"
                      :key="index + move.to"
                      v-bind:position="
                    setPieceLocationInBoard(
                      fileToNumber(move.to[0]),
                      move.to[1]
                    )
                  "
                      v-bind:attacked="validator.get(move.to) != null"
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <v-btn style="margin-top:2%;" :tile="true" color="warning" @click="resetChessboard()">RESET</v-btn>

        <!--<button @click="flipBoard()">Swap Board's side</button>
        <h1>{{debug}}</h1>-->
      </div>
    </v-col>

    <v-col cols="12" md="5" :style=" is_mobile ? 'padding:0 10%':''">
      <div :class="is_mobile ? 'movesbox_mob':'movesbox'">
        <template v-for="(move,index) in moves_history">
          <span
            :key="'n'+index"
            :class="is_mobile ? 'move_mob':'move_number'"
            class="n_move"
            v-if="move.color == 'w'"
          >{{ moves_history.indexOf(move) / 2 + 1 }}</span>
          <span :key="'m'+index" :class="is_mobile ? 'move_mob':'move'">{{ move.san }}</span>
        </template>
      </div>
    </v-col>
  </v-row>
</template>






<script>
var Chess = require("chess.js");

//only for phone purposes: schifo ma necessario.
let touchevent = null;

import Square from "./Square.vue";
import Piece from "./Piece.vue";
export default {
  name: "Chessboard",
  components: {
    Piece,
    Square
  },

  data: function() {
    return {
      //is_player_white: true,
      turn: true,
      is_in_check: false,
      has_piece_selected: false,
      piece_selected: 0,
      list_pieces: [],
      valid_moves: [],
      moves_history: [],
      validator: new Chess(),
      board_side: true,
      onPiecePlaceAudio: false,
      //TMP
      debug: ""
    };
  },

  computed: {
    is_mobile: function() {
      return this.$vuetify.breakpoint.smOnly || this.$vuetify.breakpoint.xsOnly;
    },
    board_padding: function() {
      let toRtn = "";
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          toRtn = "10%";
          break;

        case "sm":
          toRtn = "25%";
          break;

        case "md":
          toRtn = "10%";
          break;

        case "lg":
          toRtn = "10%";
          break;

        case "xl":
          toRtn = "25%";
          break;
      }
      return toRtn;
    }
  },

  mounted() {
    this.initChessboard(true);

    this.$nextTick(function() {
      this.onPiecePlaceAudio = new Audio(
        require("../assets/sounds/mlgTick.ogg")
      );
    });

    //@touchmove="while_selected"
  },
  methods: {
    updateState(moves) {
      this.validator.reset();
      moves.forEach(move => {
        this.validator.move(move, { sloppy: true });
      });

      this.list_pieces = this.initChessboard(false);
    },

    undo: function() {
      this.validator.undo();
    },
    _xPositionToFile(x_position) {
      //////window.console.log(x_position);
      var rank = "h";
      if (x_position == 1) {
        rank = "a";
      } else if (x_position == 2) {
        rank = "b";
      } else if (x_position == 3) {
        rank = "c";
      } else if (x_position == 4) {
        rank = "d";
      } else if (x_position == 5) {
        rank = "e";
      } else if (x_position == 6) {
        rank = "f";
      } else if (x_position == 7) {
        rank = "g";
      }
      return rank;
    },
    got_selected: function(e) {
      /*var height = this.$parent.$el.offsetY;
      var width = this.$parent.$el.offsetX;

      if (this.has_piece_selected) {
        ////////window.console.log(e.clientX + " " + e.clientY);
        //hei:100=x:position.y
        this.left_pos = e.offsetX - width;
        ////////window.console.log(this.left_pos + " " + this.top_pos);
        this.top_pos = e.offsetY - height;
      }
      //////window.console.log("Posizioni: " + this.left_pos + " - " + this.top_pos);
      //console.log(e);*/
      if (e.target.className != "cbrd") {
        //let selectedPieceColor = e.target.className[1] == "w";

        /*if (
          !(
            (selectedPieceColor && this.is_player_white) ||
            (!selectedPieceColor && !this.is_player_white)
          )
        ) {
          return;
        }*/

        if (
          this.validator.in_draw() ||
          this.validator.in_stalemate() ||
          this.validator.in_threefold_repetition()
        ) {
          return;
        }

        if (this.validator.in_checkmate()) {
          //window.console.log(this.validator.in_checkmate());
          return;
        }

        /*if (
          (this.turn && !this.is_player_white) ||
          (!this.turn && this.is_player_white)
        ) {
          return;
        }*/
        this.has_piece_selected = true;
        var id = e.target.className.split(" ")[1];

        var piece = this.getPieceById(id);
        window.console.log(piece);
        this.getValidMoves(piece.position.x, piece.position.y);
        var piece_ref = this.$refs[id][0];
        this.piece_selected = piece_ref;
        ////////window.console.log(e);
        ////////window.console.log(piece_ref);
        /*this.piece_selected.set_mouse_pos(
          e.clientY -
            this.piece_selected.$el.clientHeight / 2 -
            (e.clientY - e.layerY),
          e.clientX - this.piece_selected.$el.clientHeight / 2
        );*/
        ////////window.console.log(e.screenY - e.clientY);
        var rect = e.target.offsetParent.getBoundingClientRect();
        if (e.type === "mousedown") {
          this.piece_selected.got_selected(false);
          this.piece_selected.set_mouse_pos(
            e.clientY - rect.top - this.piece_selected.$el.clientHeight / 2,
            e.clientX - rect.left - this.piece_selected.$el.clientHeight / 2
          );
        } else {
          document.body.classList.toggle("stop-scroll");
          touchevent = e;
          this.piece_selected.got_selected(true);
          window.console.log(e);
          this.piece_selected.set_mouse_pos(
            e.touches[0].clientY -
              rect.top -
              this.piece_selected.$el.clientHeight,
            e.touches[0].clientX -
              rect.left -
              this.piece_selected.$el.clientHeight / 2
          );
        }
      }
    },
    logListPieces() {
      /*for (var i = 0; i < this.list_pieces.length; i++) {
        window.console.log(
          " posizione in array: " +
            i +
            " - id: " +
            this.list_pieces[i].id +
            " - positione: " +
            this._xPositionToFile(this.list_pieces[i].position.x) +
            "" +
            this.list_pieces[i].position.y
        );
      }*/
      window.console.log(this.validator.ascii());
      window.console.log("\n");
    },
    stopDrag: function(e) {
      //window.console.log(e);
      if (this.has_piece_selected) {
        var rect = e.target.offsetParent.getBoundingClientRect();
        var dim = e.target.offsetParent.clientWidth;
        ////////window.console.log(e);
        var posY = 0;
        var posX = 0;
        let isTouchInput = false;
        if (e.type === "mouseup") {
          posY = e.clientY - rect.top;
          posX = e.clientX - rect.left;
        } else {
          document.body.classList.toggle("stop-scroll");
          isTouchInput = true;
          window.console.log(window.TouchEvent);
          window.console.log(e);
          window.console.log(touchevent);

          //Schifo
          posY = touchevent.changedTouches[0].pageY - rect.top;
          posX = touchevent.changedTouches[0].pageX - rect.left;
          this.debug = posY + " - " + posX;
          window.console.log(this.debug);
        }

        ////////window.console.log(dim);
        ////////window.console.log(posX + " - " + posY);
        var id = this.piece_selected.id;

        //////window.console.log(this.piece_selected);
        var indexInList = this.list_pieces.indexOf(this.getPieceById(id));
        //////window.console.log(indexInList);
        //////window.console.log(" ------");
        //////window.console.log(id);
        let new_positionY = 0;

        let new_positionX = 0;

        if (this.board_side) {
          new_positionY = 8 - (Math.ceil((posY * 800) / dim / 100.0) - 1);
          new_positionX = Math.ceil((posX * 800) / dim / 100.0);
        } else {
          new_positionY = Math.ceil((posY * 800) / dim / 100.0);
          new_positionX = 8 - (Math.ceil((posX * 800) / dim / 100.0) - 1);
        }
        //////window.console.log(new_positionX + "-" + new_positionY);
        var to_file = this._xPositionToFile(new_positionX);
        ////////window.console.log(to_file);
        var to_rank = new_positionY;
        //////window.console.log(this.list_pieces[indexInList].position.x);
        var from_file = this._xPositionToFile(
          this.list_pieces[indexInList].position.x
        );
        var from_rank = this.list_pieces[indexInList].position.y;

        //////window.console.log(from_file + from_rank);
        //////window.console.log(to_file + to_rank);

        var valid_move = this.getValidMove({
          from: from_file + from_rank,
          to: to_file + to_rank
        });

        this.debug = valid_move;

        this.makeMove(valid_move, {
          posX: posX,
          posY: posY,
          new_positionX: new_positionX,
          new_positionY: new_positionY,
          from_file: from_file,
          from_rank: from_rank,
          to_file: to_file,
          to_rank: to_rank,
          indexInList: indexInList,
          input: isTouchInput
        });
        //this.setCheck(false);
        ////////window.console.log(to_file + to_rank);
        //window.console.log(this.getPieceIDByColorAndType("b", "k"));
        /*let refid = this.getPieceIDByColorAndType("b", "k");
        this.$refs[refid][0].$el.className =
          this.$refs[refid][0].$el.className + " check";*/
      }
      //window.console.log(this.validator.game_over());
    },
    while_selected: function(e) {
      if (this.piece_selected != 0) {
        //window.console.log(e.type);
        ////////window.console.log(e.clientY);
        ////////window.console.log(e.clientY);
        var rect = e.target.offsetParent.getBoundingClientRect();

        if (e.type === "mousemove") {
          this.piece_selected.set_mouse_pos(
            e.y - rect.top - this.piece_selected.$el.clientHeight / 2,
            e.x - rect.left - this.piece_selected.$el.clientHeight / 2
          );
        } else {
          touchevent = e;
          this.piece_selected.set_mouse_pos(
            e.touches[0].clientY -
              rect.top -
              this.piece_selected.$el.clientHeight,
            e.touches[0].clientX -
              rect.left -
              this.piece_selected.$el.clientHeight / 2
          );
        }
      }
    },
    getValidMove(move) {
      var found = false;
      var i = 0;
      while (!found && i < this.valid_moves.length) {
        if (
          move.from == this.valid_moves[i].from &&
          move.to == this.valid_moves[i].to
        ) {
          found = true;
        } else {
          i = i + 1;
        }
      }
      return this.valid_moves[i];
    },
    swapPieces(isKingSide) {
      var king_id = undefined;
      var rook_id = undefined;

      if (isKingSide) {
        if (this.turn) {
          king_id = this.getPieceIDByColorAndType("w", "k");
          var king_piece_ref = this.$refs[king_id][0];
          rook_id = this.getPieceIDBySquare({ x: 8, y: 1 });
          var rook_piece_ref = this.$refs[rook_id][0];

          let new_rook_pos = this.setPieceLocationInBoard(6, 1);
          let new_king_pos = this.setPieceLocationInBoard(7, 1);
          rook_piece_ref.setPosition(new_rook_pos.y, new_rook_pos.x);
          king_piece_ref.setPosition(new_king_pos.y, new_king_pos.x);

          let king_pos_list = this.getPiecePositionInListById(king_id);
          let rook_pos_list = this.getPiecePositionInListById(rook_id);
          this.list_pieces[king_pos_list].position.x = 7;
          this.list_pieces[king_pos_list].position.y = 1;
          this.list_pieces[rook_pos_list].position.x = 6;
          this.list_pieces[rook_pos_list].position.y = 1;
        } else {
          king_id = this.getPieceIDByColorAndType("b", "k");
          king_piece_ref = this.$refs[king_id][0];
          rook_id = this.getPieceIDBySquare({ x: 8, y: 8 });
          rook_piece_ref = this.$refs[rook_id][0];
          let new_rook_pos = this.setPieceLocationInBoard(6, 8);
          let new_king_pos = this.setPieceLocationInBoard(7, 8);

          rook_piece_ref.setPosition(new_rook_pos.y, new_rook_pos.x);
          king_piece_ref.setPosition(new_king_pos.y, new_king_pos.x);

          let king_pos_list = this.getPiecePositionInListById(king_id);
          let rook_pos_list = this.getPiecePositionInListById(rook_id);
          this.list_pieces[king_pos_list].position.x = 7;
          this.list_pieces[king_pos_list].position.y = 8;
          this.list_pieces[rook_pos_list].position.x = 6;
          this.list_pieces[rook_pos_list].position.y = 8;
        }
      } else {
        if (this.turn) {
          king_id = this.getPieceIDByColorAndType("w", "k");
          king_piece_ref = this.$refs[king_id][0];
          rook_id = this.getPieceIDBySquare({ x: 1, y: 1 });
          rook_piece_ref = this.$refs[rook_id][0];

          let new_rook_pos = this.setPieceLocationInBoard(4, 1);
          let new_king_pos = this.setPieceLocationInBoard(3, 1);
          rook_piece_ref.setPosition(new_rook_pos.y, new_rook_pos.x);
          king_piece_ref.setPosition(new_king_pos.y, new_king_pos.x);

          let king_pos_list = this.getPiecePositionInListById(king_id);
          let rook_pos_list = this.getPiecePositionInListById(rook_id);
          this.list_pieces[king_pos_list].position.x = 3;
          this.list_pieces[king_pos_list].position.y = 1;
          this.list_pieces[rook_pos_list].position.x = 4;
          this.list_pieces[rook_pos_list].position.y = 1;
        } else {
          king_id = this.getPieceIDByColorAndType("b", "k");
          king_piece_ref = this.$refs[king_id][0];
          rook_id = this.getPieceIDBySquare({ x: 1, y: 8 });
          rook_piece_ref = this.$refs[rook_id][0];
          let new_rook_pos = this.setPieceLocationInBoard(4, 8);
          let new_king_pos = this.setPieceLocationInBoard(3, 8);
          rook_piece_ref.setPosition(new_rook_pos.y, new_rook_pos.x);
          king_piece_ref.setPosition(new_king_pos.y, new_king_pos.x);

          let king_pos_list = this.getPiecePositionInListById(king_id);
          let rook_pos_list = this.getPiecePositionInListById(rook_id);
          this.list_pieces[king_pos_list].position.x = 3;
          this.list_pieces[king_pos_list].position.y = 8;
          this.list_pieces[rook_pos_list].position.x = 4;
          this.list_pieces[rook_pos_list].position.y = 8;
        }
      }
    },
    getPieceByPosition(position) {
      var found = false;
      var i = 0;
      var toRtn = undefined;

      ////////window.console.log(this.list_pieces);
      while (!found && i < this.list_pieces.length) {
        if (
          this.list_pieces[i].position.x == position.x &&
          this.list_pieces[i].position.y == position.y
        ) {
          toRtn = this.list_pieces[i];
          found = true;
        } else {
          i = i + 1;
        }
      }
      ////////window.console.log(toRtn);
      return toRtn;
    },
    getPieceById(id) {
      var toRtn = undefined;
      var found = false;
      var i = 0;
      while (!found) {
        if (parseInt(this.list_pieces[i].id) == id) {
          toRtn = this.list_pieces[i];
          found = true;
        } else {
          i = i + 1;
        }
      }
      return toRtn;
    },

    getValidMoves(x, y) {
      window.console.log(x, y);
      var file = this._xPositionToFile(x);
      var move = file + y;
      ////////window.console.log(y);
      var validMoves = this.validator.moves({
        verbose: true,
        square: move
      });
      ////////window.console.log(validMoves);
      validMoves.forEach(move => {
        ////////window.console.log(move);
        ////////window.console.log(this.valid_moves);
        ////////window.console.log(this.valid_moves);
        this.valid_moves.push({
          from: move.from,
          to: move.to,
          flags: move.flags
        });
      });
      //window.console.log(validMoves);
    },
    getPiecePositionInListById(id) {
      var found = false;
      var i = 0;
      while (!found) {
        if (parseInt(this.list_pieces[i].id) == id) {
          found = true;
        } else {
          i = i + 1;
        }
      }
      return i;
    },
    setPieceLocationInBoard(x, y) {
      var toRtn = { x: 0, y: 0 };

      /*////////window.console.log(id);
      var chunk = this.board_size / 8;
      toRtn.x = chunk * x - chunk;
      toRtn.y = this.board_size - chunk * y;
      ////////window.console.log(toRtn.x + " " + toRtn.y + " " + x + "," + y);*/
      ////////window.console.log(x + y);

      /*toRtn.x = x * 100 - 100;
      toRtn.y = (8 - y) * 100;*/

      if (this.board_side) {
        //lato bianco
        toRtn.x = x * 100 - 100;
        toRtn.y = (8 - y) * 100;
      } else {
        //window.console.log("CIAO");
        //lato nero

        //devo flippare

        toRtn.y = y * 100 - 100;

        toRtn.x = (8 - x) * 100;
      }
      //window.console.log(x, y, toRtn);
      return toRtn;
    },
    fileToNumber(file) {
      var toRtn = 8;
      if (file == "a") {
        toRtn = 1;
      } else if (file == "b") {
        toRtn = 2;
      } else if (file == "c") {
        toRtn = 3;
      } else if (file == "d") {
        toRtn = 4;
      } else if (file == "e") {
        toRtn = 5;
      } else if (file == "f") {
        toRtn = 6;
      } else if (file == "g") {
        toRtn = 7;
      } else {
        toRtn = 8;
      }
      return toRtn;
    },
    resetChessboard() {
      if (this.validator.in_checkmate()) {
        this.$refs.chessBoard.classList.toggle("gameover");
      }

      this.validator = new Chess();
      this.moves_history = [];
      this.board_side = true;
      this.list_pieces = [];

      this.initChessboard(true);
    },
    initChessboard() {
      let board = undefined;

      board = this.validator;
      window.console.log(board.ascii());

      var pieces_counter = 0;

      for (var i = 0; i < 64; i++) {
        var piece = board.get(board.SQUARES[i]);
        if (piece != null) {
          var position_uci = board.SQUARES[i];
          var rank = parseInt(position_uci[1]);
          var file = position_uci[0];

          file = this.fileToNumber(file);

          var toPush = {
            id: pieces_counter,
            type: piece.type,
            color: piece.color,
            position: { x: file, y: rank }
          };
          ////////window.console.log(toPush);
          this.list_pieces.push(toPush);
          pieces_counter = pieces_counter + 1;
        }
      }
      window.console.log("INIT");
    },
    getPieceIDByColorAndType(color, type) {
      let found = false;
      let i = 0;
      let toRtn = undefined;
      while (!found && i < this.list_pieces.length) {
        if (
          this.list_pieces[i].color === color &&
          this.list_pieces[i].type === type
        ) {
          found = true;
          toRtn = this.list_pieces[i].id;
        } else {
          i = i + 1;
        }
      }
      if (found) return toRtn;
      else return false;
    },
    toggleCheck(side) {
      let color = side ? "w" : "b";
      let refid = this.getPieceIDByColorAndType(color, "k");
      window.console.log(typeof this.$refs[refid][0].$el.classList);
      this.$refs[refid][0].$el.classList.toggle("check");
    },
    flipBoard() {
      this.board_side = !this.board_side;
      //window.console.log(this.board_side);

      this.list_pieces.forEach(piece => {
        //window.console.log(this.$refs[piece.id][0]);
        let position = this.setPieceLocationInBoard(
          piece.position.x,
          piece.position.y
        );
        this.$refs[piece.id][0].setPosition(position.y, position.x);
      });
    },
    getPieceIndexBySquare(square) {
      let found = false;
      let i = 0;
      while (!found && i < this.list_pieces.length) {
        if (
          this.list_pieces[i].position.x == square.x &&
          this.list_pieces[i].position.y == square.y
        ) {
          found = true;
        } else {
          i = i + 1;
        }
      }
      return i;
    },
    pushMove: function(move) {
      this.moves_history.push(move);
    },
    getPieceIDBySquare(square) {
      let found = false;
      let i = 0;
      let toRtn = 0;
      while (!found && i < this.list_pieces.length) {
        if (
          this.list_pieces[i].position.x == square.x &&
          this.list_pieces[i].position.y == square.y
        ) {
          found = true;
          toRtn = this.list_pieces[i].id;
        } else {
          i = i + 1;
        }
      }
      return toRtn;
    },
    makeMove(valid_move, params = undefined) {
      window.console.log(valid_move);
      let touchInput = undefined;
      let flags = "";

      if (params == undefined) {
        window.console.log(this.validator.moves());
        if (valid_move.length > 4) {
          //b7a8q+
          //b7a8a
          if (valid_move.length > 5) {
            flags = "=" + valid_move[4] + valid_move[5];
          } else {
            flags = flags = "=" + valid_move[4];
          }
          valid_move = valid_move.substring(0, valid_move.length - 1);
        }
        window.console.log(valid_move);
        let tmp = this.validator.move(valid_move + flags, { sloppy: true });
        window.console.log(tmp);
        valid_move = tmp.san;
        if (tmp == null) return;
        this.validator.undo();
      }
      if (params != undefined) touchInput = params.input;

      if (valid_move != undefined && valid_move != null) {
        let dim = this.$refs.chessBoard.clientHeight;
        //window.console.log(this.mossa);
        //window.console.log(valid_move);
        let tmp = {
          posX: 0,
          posY: 0,
          new_positionX: 0,
          new_positionY: 0,
          from_file: 0,
          from_rank: 0,
          to_file: 0,
          to_rank: 0,
          indexInList: 0
        };
        if (params == undefined) {
          console.log(this.validator.moves());
          valid_move = this.validator.move(valid_move);
          window.console.log(valid_move);
          let pos_x = this.fileToNumber(valid_move.from[0]);
          let pos_y = valid_move.from[1];
          tmp.new_positionX = this.fileToNumber(valid_move.to[0]);
          tmp.new_positionY = valid_move.to[1];
          tmp.from_file = valid_move.from[0];
          tmp.from_rank = valid_move.from[1];
          tmp.to_file = valid_move.to[0];
          tmp.to_rank = valid_move.to[1];
          window.console.log(tmp.from_file + tmp.from_rank);
          window.console.log(tmp.to_file + tmp.to_rank);
          let positionsInBoard = this.setPieceLocationInBoard(
            tmp.new_positionX,
            tmp.new_positionY
          );

          tmp.posX = positionsInBoard.x;
          tmp.posY = positionsInBoard.y;
          window.console.log({ x: pos_x, y: pos_y });

          this.piece_selected = this.$refs[
            this.getPieceIDBySquare({ x: pos_x, y: parseInt(pos_y) })
          ][0];
          window.console.log(this.piece_selected);

          tmp.indexInList = this.getPieceIndexBySquare({ x: pos_x, y: pos_y });
          this.validator.undo();
        } else {
          tmp = {
            posX: params.posX,
            posY: params.posY,
            new_positionX: params.new_positionX,
            new_positionY: params.new_positionY,
            from_file: params.from_file,
            from_rank: params.from_rank,
            to_file: params.to_file,
            to_rank: params.to_rank,
            indexInList: params.indexInList
          };

          tmp.posY = Math.ceil((tmp.posY * 800) / dim / 100.0) * 100 - 100;
          tmp.posX = Math.ceil((tmp.posX * 800) / dim / 100.0) * 100 - 100;
        }
        //window.console.log("valida");
        //check for enpassant captures:
        var en_passant_norm = 0;
        var promotion = false;
        if (valid_move.flags.includes("e")) {
          if (this.turn) {
            en_passant_norm = -1;
          } else {
            en_passant_norm = 1;
          }
        }
        if (en_passant_norm == 0) {
          //check for promotions
          if (valid_move.flags.includes("p")) {
            //promozione.
            promotion = true;
          }
        }
        var move = !promotion
          ? {
              from: tmp.from_file + tmp.from_rank,
              to: tmp.to_file + tmp.to_rank
            }
          : {
              from: tmp.from_file + tmp.from_rank,
              to: tmp.to_file + tmp.to_rank,
              promotion: "q"
            };
        window.console.log(move);
        let move_done = this.validator.move(move);
        if (valid_move.flags.includes("k")) {
          if (this.turn) {
            //king side white
            this.swapPieces(true);
          } else {
            this.swapPieces(true);
          }
        } else if (valid_move.flags.includes("q")) {
          if (this.turn) {
            this.swapPieces(false);
          } else {
            this.swapPieces(false);
          }
        } else {
          var piece_in_position = this.getPieceByPosition({
            x: tmp.new_positionX,
            y: parseInt(tmp.new_positionY) + en_passant_norm
          });

          window.console.log(piece_in_position);
          //window.console.log(piece_in_position);
          window.console.log(
            tmp.new_positionX + "    " + tmp.new_positionY + en_passant_norm
          );
          if (promotion) {
            this.list_pieces[tmp.indexInList].type = "q";
            this.piece_selected.className = "q" + this.turn ? "w" : "b";
          }
          if (piece_in_position != undefined) {
            ////////window.console.log(piece_in_position);
            //this.logListPieces();
            var pos = this.list_pieces.indexOf(piece_in_position);
            ////window.console.log(pos);
            this.list_pieces[tmp.indexInList].position.x = tmp.new_positionX;
            this.list_pieces[tmp.indexInList].position.y = tmp.new_positionY;
            if (tmp.indexInList > -1) this.list_pieces.splice(pos, 1); //posizione 13
            //////////window.console.log(this.getPieceById(14));

            ////////window.console.log(this.list_pieces);
            //this.logListPieces();
          } else {
            //this.logListPieces();
            this.list_pieces[tmp.indexInList].position.x = tmp.new_positionX;
            this.list_pieces[tmp.indexInList].position.y = tmp.new_positionY;
            //this.logListPieces();
          }
          window.console.log(tmp.posX, tmp.posY);
          this.debug = tmp.posX + "-" + tmp.posY;

          this.piece_selected.setPosition(tmp.posY, tmp.posX);

          this.debug = "2";

          //window.console.log(valid_move);
        }
        this.pushMove({
          pieceType: this.list_pieces[tmp.indexInList].type,
          color: this.list_pieces[tmp.indexInList].color,
          from_file: tmp.from_file,
          from_rank: tmp.from_rank,
          to_file: tmp.to_file,
          to_rank: tmp.to_rank,
          captured: piece_in_position,
          flags: valid_move.flags,
          san: move_done.san
        });

        this.debug = "3";

        //window.console.log(moveData);
        //EMIT DATA
        this.turn = !this.turn;

        //

        //audio MLG
        if (this.onPiecePlaceAudio) {
          this.onPiecePlaceAudio.play();
        }

        this.debug = "4";
        this.flipBoard();

        if (this.validator.in_check()) {
          if (this.is_in_check) {
            this.toggleCheck(!this.turn);
          }
          this.is_in_check = true;
          this.toggleCheck(this.turn);
        } else if (this.is_in_check) {
          this.toggleCheck(!this.turn);
          this.is_in_check = false;
        }

        if (this.validator.in_checkmate()) {
          let audio = new Audio(require("../assets/sounds/gameover.mp3"));
          audio.play();
          //document.body.classList.toggle("gameover");
          this.$refs.chessBoard.classList.toggle("gameover");
        }
        //this.logListPieces();}
      }
      this.has_piece_selected = false;
      if (params != undefined) {
        this.piece_selected.got_unselected(touchInput);
      }
      this.piece_selected = 0;
      if (params != undefined) {
        var n_moves = this.valid_moves.length;
        while (n_moves >= 0) {
          this.valid_moves.pop();
          n_moves = n_moves - 1;
        }
      }

      window.console.log(this.validator.ascii());
    },
    debugMakeMove() {
      this.makeMove(this.debug[0]);
      this.debug.shift();
    }
  }
};
</script>
