import Vue from "vue";
import App from "./App.vue";
import Vuex from "vuex";
import createPersistedState from 'vuex-persistedstate';
import Router from "vue-router";
//delete this import VueCookie from "vue-cookie";
import VueCookies from "vue-cookies";

import Chessboard from "./components/Chessboard.vue";
import Signin from "./components/Signin.vue";
import Game from "./components/Game.vue";
import GameFinder from "./components/GameFinder.vue";

import vuetify from '@/plugins/vuetify' // path to vuetify export
Vue.config.productionTip = false;
Vue.use(Vuex);
Vue.use(Router);
//Vue.use(VueCookie);
Vue.use(VueCookies);


const store = new Vuex.Store({
  state: {
    token: null
  },
  mutations: {
    SET_TOKENS: function (state, token) {
      //state.username = username;
      state.token = token;
      window.console.log(state.token);

    },
    DELETE_TOKENS: function (state, token) {
      state[token] = null;
    }
  },
  getters: {
    tokens: state => { return state.token }
  },
  actions: {
    LOGIN: (context, token) => {
      context.commit("SET_TOKENS", token);
    },
    LOGOUT: (context, token) => {
      //remove cookie


      //clear username
      context.commit("DELETE_TOKENS", token);
    }
  },
  plugins: [
    createPersistedState({
      getState: (key) => window.$cookies.get(key),
      setState: (key, token) => window.$cookies.set(key, token),
      removeState: (key) => window.$cookies.remove(key)
    })
  ]
});




const router = new Router({
  routes: [
    { path: "/", name: "homepage", component: Chessboard },
    { path: "/board", name: "board", component: Chessboard },
    {
      path: "/signin", name: "signin", component: Signin
    }, {
      path: "/game/:id?", name: "game", component: Game, props: true
    },
    { path: "/find_game", name: "find_game", component: GameFinder }
  ]
});

new Vue({
  render: h => h(App),
  store: store,
  router, vuetify
}).$mount("#app");
