      <v-divider></v-divider>

      <v-list>
        <v-list-item :to="item.link" link v-for="item in drawerItems" :key="item.title">
          <v-list-item-icon>
            <v-icon>{{item.icon}}</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>{{ item.title }}</v-list-item-title>
