        </v-list-item>
        <v-list-item link @click="auth()">
          <v-list-item-icon>
            <v-icon>{{isLoggedIn ? "exit_to_app":"account_circle"}}</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>{{isLoggedIn ? "Logout":"Login"}}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
    <v-app-bar dense app color="primary">
      <v-app-bar-nav-icon @click.stop="drawer = !drawer"></v-app-bar-nav-icon>
      <v-toolbar-title>PPII</v-toolbar-title>
      <!--
      <v-spacer></v-spacer>

      <v-menu left bottom>
        <template v-slot:activator="{ on }">
          <v-btn icon v-on="on">
            <v-icon>notifications</v-icon>
          </v-btn>
        </template>

        <v-list>
          <v-list-item v-for="n in 5" :key="n" @click="() => {}">
            <v-list-item-title>Option {{ n }}</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>-->
    </v-app-bar>

    <v-content>
      <router-view ref="view"></router-view>
      <v-snackbar v-model="events.sfida.arrivata" :timeout="4000" color="info">Sei stato sfidato!</v-snackbar>
      <v-snackbar
        v-model="events.partitaIniziata.arrivata"
        :timeout="4000"
        color="info"
        @click="goToEvent(false)"
      >Hai inziato una partita!</v-snackbar>
    </v-content>
  </v-app>
</template>

<script>
export default {
  name: "App",
  data: function() {
    return {
      drawer: false,
      drawerItems: [
        { title: "Homepage", icon: "home", link: "/" },
        { title: "Practice", icon: "school", link: "/game" },
        { title: "Play", icon: "games", link: "/find_game" }
      ],
      isLoggedIn: false,
      account: {
        id: "",
        username: "",
        online: true,
        perfs: {
          blitz: {
            games: 0,
            rating: 0,
            rd: 0,
            prog: 0
          },
          puzzle: {
            games: 0,
            rating: 0,
            rd: 0,
            prog: 0
          },
          atomic: {
            games: 0,
            rating: 0,
            rd: 0,
            prog: 0,
            prov: false
          },
          ultraBullet: {
            games: 0,
            rating: 0,
            rd: 0,
            prog: 0,
            prov: false
          },
          bullet: {
            games: 0,
            rating: 0,
            rd: 0,
            prog: 0
          },
          correspondence: {
            games: 0,
            rating: 0,
            rd: 0,
            prog: 0,
            prov: false
          },
          classical: {
            games: 0,
            rating: 0,
            rd: 0,
            prog: 0,
            prov: false
          },
          rapid: {
            games: 0,
            rating: 0,
            rd: 0,
            prog: 0,
            prov: false
          }
        },
        createdAt: 0,
        profile: {
          country: "",
          location: "",
          bio: "",
          firstName: "",
          lastName: "",
          links: ""
        },
        seenAt: 0,
        playTime: {
          total: 0,
          tv: 0
        },
        language: "",
        url: "",
        nbFollowing: 0,
        nbFollowers: 0,
        completionRate: 0,
        count: {
          all: 0,
          rated: 0,
          ai: 0,
          draw: 0,
          drawH: 0,
          loss: 0,
          lossH: 0,
          win: 0,
          winH: 0,
          bookmark: 0,
          playing: 0,
          import: 0,
          me: 0
        },
        followable: false,
        following: false,
        blocking: false,
        followsYou: false
      },

      events: {
        sfida: { arrivata: false, value: "" },
        partitaIniziata: { arrivata: false, value: "" }
      }
    };
  },
  methods: {
    auth: function() {
      if (this.isLoggedIn) {
        // Redirect to next page
        window.location = "http://localhost:8080/?signout=";
      } else {
        window.location.href = "http://localhost:8087/auth";
      }
    },
    goToEvent(isChallenge) {
      if (!isChallenge) {
        window.console.log(
          "id partita iniziata : " + this.events.partitaIniziata.value
        );

        this.$router.push({
          name: "game",
          params: { id: this.events.partitaIniziata.value }
        });
      }
    },
    getAccountData: async function() {
      const axios = require("axios");
      //CARICA LE API CHE SEI CONNESSO E NON DEVI FARE REDIRECT
      let response = await axios.get("/api/account", {
        baseURL: "https://lichess.org/",
        headers: { Authorization: "Bearer " + this.$store.getters.tokens }
      });
      this.account = response.data;
    },

    listenForEvents: async function() {
      //CARICA LE API CHE SEI CONNESSO E NON DEVI FARE REDIRECT
      const ndjsonStream = require("can-ndjson-stream");
      fetch("https://lichess.org/api/stream/event", {
        headers: {
          Authorization: "Bearer " + this.$store.getters.tokens
        }
      })
        .then(response => {
          return ndjsonStream(response.body); //ndjsonStream per leggere response.body
        })
        .then(exampleStream => {
          const reader = exampleStream.getReader();
          let read;
          reader.read().then(
            (read = result => {
              if (result.done) {
                return;
              }
              //new event found.

              this.gotEvent(result.value);
              reader.read().then(read);
            })
          );
        })
        .catch(e => {
          window.console.log("Errore: " + e);
        });
    },
    gotEvent(event) {
      window.console.log(this.$route);
      window.console.log(event);
      window.console.log(event.type);
      window.console.log(this.$refs);
      let eventType = event.type;
      window.console.log(eventType);
      if (eventType == "gameStart") {
        window.console.log(eventType + "QUA");

        //arrivata una notifica di inizio gioco
        if (this.$route.name == "game") {
          //arrivata una notifica e sono nella pagina game
          //this.$refs["view"].changeID( event.game.id);
          this.$router.push({ name: "game", params: { id: event.game.id } });
          //funziathis.$router.push({ name: 'user', params: { userId: 123 }})
          window.console.log("ho trovato la partita che ho cercato");
        } else {
          window.console.log(" arrivata una sfida");
          this.events.partitaIniziata.arrivata = true;
          this.events.partitaIniziata.value = event.game.id;
        }
      } else {
        window.console.log("not gamestart");
      }
    }
  },

  created: function() {
    window.console.log(this.$route);
    const urlParams = new URLSearchParams(window.location.search);
    const is_signing_out = urlParams.get("signout");
    window.console.log(is_signing_out);

    if (is_signing_out != null) {
      this.$store.dispatch("LOGOUT", "token");
      history.pushState({}, null, "/");
      this.isLoggedIn = false;
      return;
    }

    const access_token = urlParams.get("tok");
    window.console.log(access_token);

    let bThereIsNotToken = this.$store.getters.tokens == null;

    if (bThereIsNotToken) {
      //non ci sono tokens salvati nello store.

      //window.console.log(access_token);

      if (access_token != null) {
        //ci sono dati dei token nell'URL, vuol dire che ha fatto il login.
        //window.console.log("LOGIN");

        this.$store.dispatch("LOGIN", access_token);
        history.pushState({}, null, "/");
      }
    } else {
      if (access_token != null) {
        history.pushState({}, null, "/#");
      }
    }
    //window.location.href = "http://localhost:8080";

    if (this.$store.getters.tokens == null) {
      this.isLoggedIn = false;
    } else {
      this.getAccountData();
      this.listenForEvents();
      this.isLoggedIn = true;
    }
  }
};
</script>
